const keyword_extractor = require("keyword-extractor");

const fullScanContainer = document.querySelector('[js-seo-lite-full-scan]');

if(fullScanContainer) {

    function getTemplate(name, content) {
        return `
        <table class="audit" cellspacing="0">
            <thead>
                <tr>
                    <th>${name}</th>
                </tr>
            </thead>
            <tbody>
                ${content}
            </tbody>
        </table>
        `;
    }
    
    function getMeta(auditPage) {
        const html = `
            <tr>
                <td class="audit__status has--detail">
                ${auditPage.firstChild.tagName === undefined ? `<div class="audit__status-item has--content is--full" data-name="The doctype is placed at first in the HTML code."></div>` : `<div class="audit__status-item has--content" data-name="The doctype is not placed at first in the HTML code, pleace add '<!DOCTYPE html>'."></div>`}
                </td>
            </tr>
            <tr>
                <td class="audit__status has--detail">
                    ${auditPage.querySelector('link[rel=icon]') ? `<div class="audit__status-item has--content is--full" data-name="The favicon is linked."></div>` : `<div class="audit__status-item has--content" data-name="The favicon is not linked."></div>`}
                </td>
            </tr>

            
            <tr>
                <td class="audit__status has--detail">
                ${auditPage.querySelector('meta[charset=utf-8]') ? `<div class="audit__status-item has--content is--full" data-name="The charset encoding (UTF-8) is set correctly."></div>` : `<div class="audit__status-item has--content" data-name="The charset encoding (UTF-8) is set incorrectly."></div>`}
                </td>
            </tr>
            <tr>
                <td class="audit__status has--detail">
                    <div class="audit__status-item has--content ${auditPage.documentElement.getAttribute('lang') ? `is--info` : `` }" data-name="The following language is defined by HTML: ${auditPage.documentElement.getAttribute('lang')}"></div>
                </td>
            </tr>`;

        fullScanContainer.insertAdjacentHTML("beforeend", getTemplate(`Meta specifications`, html));
    }

    function getKeywords(auditPage) {
        const script = auditPage.body.querySelector('script'); 
        const nav = auditPage.body.querySelector('nav'); 
        const footer = auditPage.body.querySelector('footer'); 
        if(script) script.remove();
        if(nav) nav.remove();
        if(footer) footer.remove();

        const languageNames = new Intl.DisplayNames(['en'], {
            type: 'language'
        });

        const language = auditPage.documentElement.lang ? languageNames.of(auditPage.documentElement.lang).toLowerCase().replace(/\([^()]*\)/g, '').trim() : 'english';
        const supportedLanguages = ['danish','dutch','english','french','galician','german','italian','polish','portuguese','romanian','russian','spanish','swedish','persian','arabic','czech'];
        if(supportedLanguages.indexOf(language) === -1) {
            fullScanContainer.insertAdjacentHTML("beforeend", getTemplate(`Top 10 keywords on page (${language})`, '<tr><td><i>Language not supported.</i></td></tr>'));
            return false
        }
    
        const pageBody = auditPage.body.innerText;

        //  Extract the keywords
        const extraction_result =
        keyword_extractor.extract(pageBody,{
            language: language,
            remove_digits: true,
            return_changed_case:true,
            remove_duplicates: false
        });
        
        let counts = Object.values(extraction_result.reduce((a, c) => {
            (a[c] || (a[c] = {name: c, count: 0})).count += 1; return a;}, {})).sort(({count: ac}, {count: bc}) => bc - ac);
        
        let pagination = 1;
        const perPage = 9;
        let html = `
        <tr>
            <td>
                <table class="audit" cellspacing="0">
                    <thead>
                        <tr>
                            <th>Keyword</th>
                            <th>Count</th>
                        </tr>
                    </thead>
                    <tbody js-keywords-container>`;
                        Object.entries(counts).forEach(([key, value]) => {
                            if(key > perPage) return;
                            html += `
                                <tr>
                                    <td>${value.name}</td>
                                    <td>${value.count}</td>
                                </tr>
                            `;
                        });
                    html += `
                    </tbody>
                </table>
                <button class="button button--secondary button--small" js-keywords-more>Load More Keywords</button>
            </td>
        </tr>
                `;

        fullScanContainer.insertAdjacentHTML("beforeend", getTemplate(`Top keywords on page (${language})`, html));
        const loadMore = fullScanContainer.querySelector('[js-keywords-more]');
        const keyWordsContainer = fullScanContainer.querySelector('[js-keywords-container]');
        
        loadMore.addEventListener('click', () => {
            pagination++;
            let keys = '';

            Object.entries(counts).forEach(([key, value]) => {
                if(key < ((pagination - 1) * (perPage) + 1) || (key > (pagination * perPage))) return;
                keys += `
                <tr>
                    <td>${value.name}</td>
                    <td>${value.count}</td>
                </tr>
                `;
            });
            
            keyWordsContainer.insertAdjacentHTML("beforeend", keys);
        });
    }

    function getHeadings(auditPage) {
        const firstHeading = [...auditPage.querySelectorAll('h1')];
        let firstHeadingWarning = ``;
        
        if(firstHeading.length > 1) {
            firstHeadingWarning = `<div class="audit__status-item has--content" data-name="Multiple H1 headings are not allowed."></div>`;
        } else if(firstHeading.length < 1) {
            firstHeadingWarning =  `<div class="audit__status-item has--content" data-name="There is no H1 heading."></div>`;
        } else if(firstHeading[0]?.innerText.length < 20) {
            firstHeadingWarning =  `<div class="audit__status-item has--content is--warning" data-name="The H1 heading is too short."></div>`;
        } else {
            firstHeadingWarning = `<div class="audit__status-item has--content is--full" data-name="The H1 heading is correct."></div>`;
        }

        let html = `
        <tr><td class='audit__status has--detail'>${firstHeadingWarning}</td></tr>
        <tr>
            <td>
                <table class="audit" cellspacing="0">
                    <thead>
                        <tr>
                            <th>Heading Level</th>
                            <th>Content</th>
                        </tr>
                    </thead>
                    <tbody class='c-headings'>`;
                        
        const headings = [...auditPage.querySelectorAll("h1, h2, h3, h4, h5, h6")];
        headings.forEach((heading) => {
            html += `
                <tr>
                    <td><span class='headings__heading is--${heading.tagName}'>${heading.tagName}</span></td>
                    <td>${heading.textContent}</td>
                </tr>
            `;
        })

        fullScanContainer.insertAdjacentHTML("beforeend", getTemplate(`Heading structure`, html));
    }


    fetch(fullScanContainer.getAttribute('js-seo-lite-full-scan')).then((r)=>{r.text().then((text)=>{
        const parser = new DOMParser();
        const auditPage = parser.parseFromString(text, "text/html");
        getMeta(auditPage);
        getKeywords(auditPage);
        getHeadings(auditPage);

        fullScanContainer.classList.remove('is--loading');
    })});


    const pageSpeedTab = document.querySelector('[js-page-speed-tab]');
    pageSpeedTab.addEventListener('click', () => {
        const pageSpeedContainer = document.querySelector('[js-page-speed]');
        if(pageSpeedContainer.childNodes.length === 0) runPageSpeed(pageSpeedContainer);
    });


    function runPageSpeed(pageSpeedContainer) {
        const url = setUpQuery();
        fetch(url)
        .then(response => response.json())
        .then(json => {
            pageSpeedContainer.classList.remove('is--loading');
            if(json.error) {
                pageSpeedContainer.innerHTML = `<div class='audit__status has--detail'><div class='audit__status-item has--content is--warning' data-name="${json.error.message}"></div></div>`;
            } else {
                const audit = json.lighthouseResult.audits;
                const performance = json.lighthouseResult.categories.performance;
                console.log(json);

                pageSpeedContainer.innerHTML = `
                    <div class="page-speed__inner">
                        <div class="page-speed__block is--half">
                            <table class="audit page-speed__performance" cellspacing="0">
                                <thead>
                                    <tr>
                                        <th>${performance.title}</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>
                                            <div class="page-speed__performance-results ${performance.score < 0.50 ? `is--error` : `${performance.score < 0.90 ? `is--warning` : `is--success`}`}">
                                                <div class='page-speed__performance-icon'></div>
                                                <div class="page-speed__performance-score">${performance.score * 100}</div>
                                                <div class="page-speed__performance-total">100</div>
                                            </div>
                                            <div class="page-speed__performance-meta">
                                                <div class="page-speed__performance-meta-item is--error">0-49</div>
                                                <div class="page-speed__performance-meta-item is--warning">50-89</div>
                                                <div class="page-speed__performance-meta-item is--success">90-100</div>
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div class='page-speed__block is--half'>
                            <table class="audit page-speed__screenshot" cellspacing="0">
                                <thead>
                                    <tr>
                                        <th>${audit['full-page-screenshot'].title}</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>
                                            <div class="page-speed__screenshot-screen">
                                                <div class="page-speed__screenshot-screen-inner">
                                                    <img class='page-speed__screenshot-screen-page' src='${audit['full-page-screenshot'].details.screenshot.data}' alt='${audit['full-page-screenshot'].description}' />
                                                </div>
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div class='page-speed__block'>
                            <table class="audit page-speed__statistics" cellspacing="0">
                                <thead>
                                    <tr>
                                        <th>Statistics</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>
                                            <div class='page-speed__statistics-item'>
                                                <b>${audit['first-contentful-paint'].title}</b>: ${audit['first-contentful-paint'].displayValue}

                                                <div class='page-speed__statistics-item-info' data-info='${audit['first-contentful-paint'].description}'></div>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>
                                            <div class='page-speed__statistics-item'>
                                                <b>${audit['speed-index'].title}</b>: ${audit['speed-index'].displayValue}

                                                <div class='page-speed__statistics-item-info' data-info='${audit['speed-index'].description}'></div>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>
                                            <div class='page-speed__statistics-item'>
                                                <b>${audit['largest-contentful-paint'].title}</b>: ${audit['largest-contentful-paint'].displayValue}

                                                <div class='page-speed__statistics-item-info' data-info='${audit['largest-contentful-paint'].description}'></div>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>
                                            <div class='page-speed__statistics-item'>
                                                <b>${audit['interactive'].title}</b>: ${audit['interactive'].displayValue}

                                                <div class='page-speed__statistics-item-info' data-info='${audit['interactive'].description}'></div>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>
                                            <div class='page-speed__statistics-item'>
                                                <b>${audit['total-blocking-time'].title}</b>: ${audit['total-blocking-time'].displayValue}

                                                <div class='page-speed__statistics-item-info' data-info='${audit['total-blocking-time'].description}'></div>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>
                                            <div class='page-speed__statistics-item'>
                                                <b>${audit['cumulative-layout-shift'].title}</b>: ${audit['cumulative-layout-shift'].displayValue}

                                                <div class='page-speed__statistics-item-info' data-info='${audit['cumulative-layout-shift'].description}'></div>
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <p class='page-speed__footer'>For a full test go to: <a href='https://pagespeed.web.dev/' target='_blank' class='button button--secondary button--small'>pagespeed.web.dev</a></p>
                    </div>
                `;
            }
        });
    }

    function setUpQuery() {
        const api = 'https://www.googleapis.com/pagespeedonline/v5/runPagespeed';
        const parameters = {
            url: encodeURIComponent(`${window.location.origin}${fullScanContainer.getAttribute('js-seo-lite-full-scan')}`)
        };
        let query = `${api}?`;
        for (key in parameters) {
            query += `${key}=${parameters[key]}`;
        }
        return query;
    }
}